@use './fonts' as *;
@use './functions' as f;

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: $headingfont;
    font-weight: 600;
}

h2,
h3,
h4,
h5,
h6 {
    letter-spacing: -.08rem;
}

h1 {
    font-size: clamp(1.5rem, 1.1rem + 3vw, 2.5rem);
    letter-spacing: -.1rem;
}

h2 {
    font-size: clamp(1.3rem, 1.1rem + 2vw, 2rem);
}

h3 {
    font-size: clamp(1.2rem, 1rem + 2vw, 1.5rem);
}

body {
    font-size: clamp(1.1rem, 0.9rem + 1vw, 1.3rem);
    font-family: $bodyfont;
    font-weight: 400;
}

.link-button {
    font-family: $headingfont;
    text-align: center;
    font-size: f.rem(18);

    &:hover {
        font-weight: 600;
        text-align: left;
    }
}

footer p {
    font-size: clamp(0.9rem, 0.75rem + 1vw, 1.1rem);
}

footer a {
    text-decoration: underline;

    &:hover {
        font-weight: 600;
    }
}